import React from "react";
import { TabList, Tab } from "./styledComponent";

const Tabs = ({ changeTab, isActive }) => (
  <TabList>
    <Tab
      tab="all"
      active={isActive("all")}
      onClick={() => {
        changeTab("all");
      }}
    >
      
    </Tab>
    <Tab
      tab="doing"
      active={isActive("doing")}
      onClick={() => {
        changeTab("doing");
      }}
    >
      
    </Tab>
    <Tab
      tab="done"
      active={isActive("done")}
      onClick={() => {
        changeTab("done");
      }}
    >
      
    </Tab>
  </TabList>
);

export default Tabs;
